}

.qs-month {
  padding-right: .5ex;
}

.qs-year {
  padding-left: .5ex;
}

.qs-squares {
  -webkit-filter: blur();
  filter: blur();
  flex-wrap: wrap;
  padding: .3125em;
  transition: filter .3s, -webkit-filter .3s;
  display: flex;
}

.qs-squares.qs-blur {
